@supports (animation: grow .5s cubic-bezier(.25, .25, .25, 1) forwards) {
  .ft-green-tick {
    width: 100%;
    height: 100%;
  }

  .tick {
    stroke-opacity: 0;
    stroke-dasharray: 29px;
    stroke-dashoffset: 29px;
    animation: draw .5s cubic-bezier(.25, .25, .25, 1) forwards;
    animation-delay: .6s
  }

  .circle {
    fill-opacity: 0;
    transform-origin: center;
    transform: scale(0);
    animation: grow 1s cubic-bezier(.25, .25, .25, 1.25) forwards;
  }
}

@keyframes grow {
  60% {
    transform: scale(.8);
    stroke-width: 4px;
    fill-opacity: 0;
  }
  100% {
    transform: scale(.9);
    //stroke-width: 8px;
    fill-opacity: 1;
    fill: rgba(53, 159, 34, 0.87);
  }
}

@keyframes draw {
  0%, 100% {
    stroke-opacity: 1;
  }
  100% {
    stroke-dashoffset: 0;
  }
}

